.page {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
  scroll-behavior: smooth;
  &__body {
    margin: 0;
    background-color: $page-background;
    &--with-menu {
      overflow: hidden;
    }
  }
  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(-100%);
    transition: transform 0.3s ease;

    &:target {
      transform: translateX(0%);
    }
  }

  &__section {
    margin-bottom: 92px;
    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 140px;
    }
  }
  &__section-title {
    margin: 0 0 48px;

    font-weight: 500;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #fff;
    @include onTablet {
      font-size: 48px;
      line-height: 100%;
    }
    @include onDesktop {
      font-size: 64px;
      line-height: 100%;
    }
  }
}
